.sidebar-btn {
    display: flex;
    align-items: center;
    background: transparent;
    border: none;
    border-left: 5px solid transparent;
    width: 100%;
    padding: 18px 24px;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: #a4a6b3;
    outline: none;
    text-align: left;

    svg {
        fill: #a4a6b3;
    }

    .sidebarIcon {
        margin-right: 20px;
    }



    &:hover {
        background: rgba(159, 162, 180, 0.08);
        border-left: 5px solid #dde2ff;
        color: #dde2ff;

        .sidebarIcon {
            svg {
                fill: #dde2ff;
            }
        }

    }

}