.ticket-list-item {
    display: flex;
    align-items: center;
    border-top: 1.5px solid #DFE0EB;
    padding: 10px 30px;
    box-sizing: border-box;
    cursor: pointer;

    &:hover {
        background: #F7F8FF;
    }

    .item-right {
        display: flex;
        align-items: center;
        margin-left: 300px;

        .customer {
            .customer-name {
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.2px;
                color: #252733;
            }

            .customer-time {
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 16px;
                letter-spacing: 0.1px;
                color: #C5C7CD;
            }
        }

        .date {
            margin-left: 200px;

            .date-day {
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.2px;
                color: #252733;
            }

            .date-hour {
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 16px;
                letter-spacing: 0.1px;
                color: #C5C7CD;
            }
        }

        .status {
            margin-left: 150px;
            padding: 10px;
            background: red;
            border-radius: 16px;
            font-style: normal;
            font-weight: bold;
            font-size: 11px;
            line-height: 14px;
            display: flex;
            align-items: center;
            text-align: center;
            letter-spacing: 0.5px;
            text-transform: uppercase;
            color: #FFFFFF;
        }
    }

    .item-left {

        display: flex;
        align-items: center;

        img {
            margin-right: 20px;
            width: 44px;
            height: 44px;
            object-fit: cover;
            border-radius: 50%;
        }
        .owner-title {
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
            letter-spacing: 0.2px;
            color: #252733;
        }

        .owner-activity {
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 16px;
            letter-spacing: 0.1px;
            color: #C5C7CD;
        }
    }
}