.box {
    width: 200px;
    background: #3d3d3d;
    height: 100vh;

    &-header {
        text-align: center;
        padding: 30px 0;
        box-sizing: border-box ;
        display: flex;
        align-items: center;
        h2 {
            font-style: normal;
            font-weight: bold;
            font-size: 19px;
            line-height: 24px;
            letter-spacing: 0.4px;
            color: #A4A6B3;
            opacity: 0.7;
            margin-left: 10px;
        }
    }

    .btn.active {


         


            .sidebar-btn {
                background: #F7F8FC;
                border-left: 5px solid #3d3d3d;
                color: #3d3d3d;

                .sidebarIcon {
                    svg {
                        fill: #3d3d3d;
                    }
                }
            }
        
    }
}

