.overBigSmallBox {
    width: 100%;
    border-bottom: 1px solid #DFE0EB;
    border-left: 1px solid #DFE0EB;
    box-sizing: border-box;
    
    display: flex;
    align-items: center;
    justify-content: center;
    height: 134px;
    background: #fff;
    flex-direction: column;
    cursor: pointer;

    span {
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        text-align: center;
        letter-spacing: 0.3px;
        color: #9FA2B4;

    }

    h2 {
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        text-align: center;
        letter-spacing: 0.3px;
        color: #252733;
    }

    &:hover {
        border-left: 1px solid #3751FF;
        background: #3752ff94;

        span {
            color: #fff;
        }

        h2 {
            color: #fff;
        }
    }
}