
.header {
    margin-bottom: 50px;

    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-left {

    }


    &-right {
        display: flex;
        align-items: center;
        justify-content: space-between;
        

        &-icons {
            border-right: 2px solid #DFE0EB;
            margin-right: 20px;

            a {
                margin: 0 20px;
            }


        }

        &-user {
            display: flex;
            align-items: center;
            justify-content: space-between;
            h2 {
                font-size: 18px;
            }


            img {
                overflow: hidden;
                width: 44px;
                height: 44px;
                object-fit: cover;
                border-radius: 50%;
                margin-left: 20px;
            }
        }
    }
}

