.overBox {
    width: 20%;
    border: 1px solid #DFE0EB;
    box-sizing: border-box;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 134px;
    background: #fff;
    flex-direction: column;
    cursor: pointer;

    span {
        font-style: normal;
        font-weight: bold;
        font-size: 19px;
        line-height: 24px;
        text-align: center;
        letter-spacing: 0.4px;
        color: #9FA2B4;
        margin-bottom: 20px;
    }

    h2 {
        font-style: normal;
        font-weight: bold;
        font-size: 40px;
        line-height: 50px;
        text-align: center;
        letter-spacing: 1px;
        color: #252733;
    }

    &:hover {
        border: 1px solid #3751FF;

        span {
            color: #3751FF;
        }

        h2 {
            color: #3751FF;
        }
    }
}