

.overviewBigBlock{
    background: #FFFFFF;
    border: 1px solid #DFE0EB;
    box-sizing: border-box;
    border-radius: 8px;
    width: 100%;
    display: flex;
    justify-content: space-between;

    &-left {
        width: 70%;
        

        &-header {
            padding: 32px;
            box-sizing: border-box;
            width: 100%;
            height: 134px;

            &-row1 {
                display: flex;
                align-items: center;
                margin-bottom: 8px;
                h3 {
                    font-style: normal;
                    font-weight: bold;
                    font-size: 19px;
                    line-height: 24px;
                    letter-spacing: 0.4px;
                    color: #252733;
                }
            }

            &-row2 {
                display: flex;
                align-items: center;
                justify-content: space-between;
                &-left {
                    span {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 12px;
                        line-height: 16px;
                        letter-spacing: 0.1px;
                        color: #9FA2B4;

                    }
                }


                &-right {
                    display: flex;
                    align-items: center;
                    span {
                        font-style: normal;
                        font-weight: 400;
                        font-size: 12px;
                        line-height: 15px;
                        letter-spacing: 0.1px;
                        color: #9FA2B4;
                        margin-left: 40px;
                        position: relative;
                        display: flex;
                        align-items: center;
                        cursor: pointer;


                        &:before {
                            content: "";
                            position: absolute;
                            width: 16px;
                            height: 2px;
                            background:  #9FA2B4;
                            left: -18px;
                            border-radius: 8px;
                            
                        }

                        &:hover {
                            color: #3751FF;

                            &::before {
                                background: #3751FF;
                                
                            }
                        }
                    }
                }
            }
        }
    }


    &-right {
        width: 30%;


    }
    
}